import React from "react";
import Header from "../layout/Header";
import {FlashCard} from "../components/cards/Cards.js";
import LoginForm from "../components/login/LoginForm.js";

function LoginPage() {
    return (
        <>
            <Header>
            </Header>
            <>
                <LoginForm 
                renderError = {error => {
                    const expiresIn = 2.5 - (Date.now() - error.issuedAt) / 1000;
                    return expiresIn > 0
                    ? <FlashCard type = "error" duration = {expiresIn} key = {error.issuedToken}>
                        {error.failure.response.data.message}
                    </FlashCard>
                    : null;
                }}
                />
            </>
        </>
    );
}

export default LoginPage;