import React from "react";
import {Accordion} from "../../components/accordion/Accordion.js";
import AccordionHead from "../../components/accordion/AccordionHead.js";
import AccordionIcon from "../../components/accordion/AccordionIcon.js";
import AccordionContent from "../../components/accordion/AccordionContent.js";
import StudentContainer from "../../components/student/StudentInfo.js";
import {accordions} from "../../global/Global.js";

function Accordions() {
    return (
        accordions.map(accordion => (
            <Accordion key = {accordion.id}>
                <AccordionHead title = {accordion.title}>
                    <AccordionIcon>
                        {"+"}
                    </AccordionIcon>
                </AccordionHead>
                <AccordionContent>
                    {accordion.content.map(content => (
                        <StudentContainer key = {content.id}
                            sources = {content.sources} 
                            notFound = {content.notFound} 
                            loading = {<p>Cargando...</p>}
                        >
                            {student => {
                                const formatted = content.formatAs(student);
                                return formatted ? <p>{formatted}</p> : null;
                            }}
                        </StudentContainer>
                    ))}
                </AccordionContent>
            </Accordion>
        ))
    );
}

export default Accordions;