const currentDate = new Date();
const month = currentDate.getMonth() + 1;
const year = currentDate.getFullYear();
const day = currentDate.getDate();

const programs = new Map([["math", "matemáticas"], ["reading", "lectura"], ["english", "inglés"]]);

function anivSources(program, isDay) {
    const map = new Map([[year, []], [year - 1, []]]);
    for (let back = 3; back < 15; back += 3) {
        const pastMonth = month - back;
        const anivYear = pastMonth < 0 ? year - 1: year;
        const anivMonth = ((pastMonth % 12) + 12) % 12;
        map.get(anivYear).push(anivMonth); 
    }
    return Array.from(map.entries()).map(entry => {
        const [anivYear, months] = entry;
        const anivMonths = months.join("|");
        return `?${isDay ? `date_dayOfMonth_programs>${program}>enroll=${day}&`: ``}date_month_programs>${program}>enroll=${anivMonths}&date_year_programs>${program}>enroll=${anivYear}`;
    });
}

function paySources(program) {
    return [`?compare_gt_programs>${program}>fee=programs>${program}>pay-current`];
}

function anivContent(isDay) {
    return Array.from(programs.entries()).map(([program, programName]) => {
        return {
            id: `aniv${program}`,
            sources: anivSources(program, isDay),
            notFound: `Nadie festeja su aniversario en ${programName}...`,
            formatAs: isDay ? formatAnnivDayProgram(program, programName) : formatAnnivMonthProgram(program, programName)
        }
    });
}

function payContent() {
    return Array.from(programs.entries()).map(([program, programName]) => {
        return {
            id: `pay${program}`,
            sources: paySources(program),
            notFound: `Nadie debe pagos en programa de ${programName}...`,
            formatAs: formatPayProgram(program, programName)
        }
    });
}

const accordions = [
    {
        id: "BirthMonth",
        title: "Cumpleaños del Mes",
        content: [
            {id: "birthmonth", sources: [`?date_month_birth=${month}`],
            notFound: "Nadie cumple años este mes...",
            formatAs: formatBirthMonth}
        ]
    },
    { 
        id: "AnnivMonth",
        title: "Aniversarios del Mes",
        content: anivContent(false)
    },
    { //THIS TAKES MULTIPLE PROGRAMS
        id: "PaymentMonth",
        title: "Pagos del Mes",
        content: payContent()
    },
];

const notifications = [
    {
        id: "BirthDay",
        content: [
            {id: "birthday", sources: [`?date_month_birth=${month}&date_dayOfMonth_birth=${day}`],
            notFound: "Nadie cumple años hoy...",
            formatAs: formatBirthDay}
        ]
    },
    { 
        id: "AnnivDay",
        content: anivContent(true)
    }
];

function formatBirthDay(student) {
    return `El cumpleaños de ${student.fullname} es hoy! Felicitalo(a)!`;
}

function formatBirthMonth(student) {
    const birthDate = new Date(student.birth);
    return `El cumpleaños de ${student.fullname} es ${birthDate.getUTCDate()} /  ${birthDate.getUTCMonth() + 1}`;
}

function formatAnnivDayProgram(program, programName) {
    return function formatAnnivDay(student) { 
        const enrollDate = new Date(student.programs[program].enroll);
        const monthDifference = 12 * (currentDate.getFullYear() - enrollDate.getUTCFullYear()) + currentDate.getMonth() - enrollDate.getUTCMonth();
        return `El aniversario de ${monthDifference} en programa de ${programName} meses de ${student.fullname} es hoy! Felicitalo(a)!`;
    };
}
function formatAnnivMonthProgram(program, programName) {
    return function formatAnnivMonth(student) { 
        const enrollDate = new Date(student.programs[program].enroll);
        const monthDifference = 12 * (currentDate.getFullYear() - enrollDate.getUTCFullYear()) + currentDate.getMonth() - enrollDate.getUTCMonth();
        return `El aniversario de ${monthDifference} en programa de ${programName} meses de ${student.fullname} es este mes!`;
    };
}

function formatPayProgram(program, programName) {
    return function formatPay(student) {
        return `${student.fullname} ha pagado $${student.programs[program]["pay-current"]} / $${student.programs[program].fee} en programa ${programName}`;
    };
}

export {accordions, notifications, programs};