import axios from "axios";
import {StudentContext} from "../context/StudentContext.js"
import {ToggleContext} from "../components/toggleable/Toggleable.js";
import {useState, useContext} from "react";
import {useInitializedFields} from "./useFields";

function useUpdateSubmit(studentRef, studentFields) {
    const toggleable = useContext(ToggleContext);
    const studentUUID = useContext(StudentContext).uuid;
    const [formSubmit] = useState({update: null, issued: 0});
    const [formError, setFormError] = useState({error: null, issued: 0});
    const [formFields] = useInitializedFields(studentFields, {submittedCount: formSubmit.issued, errorCount: formError.issued});
    function onUpdateSubmit(event) {
        event.preventDefault();
        const update = studentRef.current;
        let form = event.target;
        axios.put(`/api/students/update?uuid=${studentUUID}`, update)
        .then(response => { 
            form.reset(); studentFields.reset(); form = null;
            studentRef.current = {}; 
            toggleable.setStatus(0); //TOGGLE TO REMOVE EDITING VIEW    
        })
        .catch(err => {
            console.log(err)
            setFormError(previous => (
                {error: {failure: err, issuedAt: Date.now(), issuedToken: previous.issued + 1},
                issued: previous.issued + 1}
            ));
        });
    }
    const form = {submitted: formSubmit.update, error: formError.error, fields: formFields, toRegister: false};
    return [form, onUpdateSubmit];
}

export default useUpdateSubmit;