import React from "react";
import {Toggleable} from "../../components/toggleable/Toggleable";
import {Toggler, TogglerButton, Toggling} from "../../components/toggleable/Toggled.js"
import {ToggledState} from "../../components/toggleable/ToggledState.js";
import {StudentRegister} from "../../components/apps/StudentSubmit.js";
import {DocumentFields} from "../../components/apps/StudentFields.js";
import StudentSearch from "../../components/apps/StudentSearch.js";
import {FlashCard} from "../../components/cards/Cards.js";
import {programs} from "../../global/Global.js";

function HomeApps() {

    return (
        <Toggleable initialStatus = {0} className = "apps-student">
            <Toggler className = "apps-choices">
                <TogglerButton caption = "Registrar Alumno" status = {1} />
                <TogglerButton caption = "Buscar Alumno" status = {2} />
            </Toggler>
            <Toggling>
                <ToggledState status = {[1]}>
                    <StudentRegister fields = {registerFields} 
                    renders = {{
                        submitted: submitted => {
                            const now = Date.now();
                            return submitted.map(submit => {
                                const expiresIn = 2.5 - (now - submit.issuedAt) / 1000;
                                return expiresIn > 0
                                ? <FlashCard type = "success" duration = {expiresIn} key = {submit.issuedToken}>
                                    {"Alumno fue registrado correctamente!"}
                                </FlashCard>
                                : null;
                            });
                        },
                        error: error => {
                            const expiresIn = 2.5 - (Date.now() - error.issuedAt) / 1000;
                            return expiresIn > 0
                            ? <FlashCard type = "error" duration = {expiresIn} key = {error.issuedToken}>
                                {"Hubo un problema para registrar alumno!"}
                            </FlashCard>
                            : null;
                        } 
                    }}                
                    />
                </ToggledState>
                <ToggledState status = {[2]}>
                    <StudentSearch />
                </ToggledState>
            </Toggling>
        </Toggleable>
    )
}

const registerFields = new DocumentFields({}, [
    ["name", {name: "name", label: "Nombre", type: "text"}],
    ["lastname", {name: "lastname", label: "Apellido", type: "text"}], 
    ["birth", {name: "birth", label: "Fecha de Nacimiento", type: "date"}],
    ["programs", new DocumentFields({name: "programs", label: "Programas"},
        Array.from(programs.entries()).map(([program, programName]) => {
            return [program, new DocumentFields({name: program, label: programName}, [
                ["enroll", {name: `programs.${program}.enroll`, label: "Fecha de Ingreso", type: "date"}],
                ["fee", {name: `programs.${program}.fee`, label: "Cuota Mensual", type: "text"}],
                ["level", {name: `programs.${program}.level`, label: "Nivel", type: "text"}]
            ])]
        })
    )]
]);

export default HomeApps;