import React from "react";
import StudentPrograms from "./StudentPrograms.js";

function StudentFieldsForm({toRegister, fields, onStudentInput}) {
    //console.log(fields);
    const formFields = fields.receiveAll().filter(field => !(field instanceof DocumentFields));
    return (
        <div className = "student-form-fields">
            {formFields.map(field => {
                const defaultValue = field.type === "date" && field.defaultValue
                    ? field.defaultValue.split("T")[0]
                    : field.defaultValue;
                return <label key = {field.label}>
                    {field.label} 
                    <input 
                        type = {field.type}
                        name = {field.name}
                        defaultValue = {defaultValue}
                        onInput = {onStudentInput}
                        required = {true}
                    />
                </label>
            })}
            <StudentPrograms toRegister = {toRegister} fields = {fields.receiveOne("programs")} onStudentInput = {onStudentInput} />
        </div>
    );
}

class DocumentFields {
    constructor({name, label}, fields) {
        this.name = name; 
        this.label = label;
        this.wasFound = undefined;
        this.fields = new Map(fields);
    }
    reset() {
        this.receiveAll().forEach(field => {
            if (field instanceof DocumentFields) {
                field.wasFound = undefined;
                field.reset();
            }
            else delete field.defaultValue;
        });
    }
    initializeWith(model) {
        function fromModel(path) {
            const value = [path].reduce((modelField, stage) => {
                return modelField ? modelField[stage] : undefined;
            }, model);
            return value;
        }
        this.receiveAll(true).forEach(([fieldName, field]) => {
            if (field instanceof DocumentFields) {
                const modelField = fromModel(fieldName)
                if (modelField) {
                    field.wasFound = true
                    field.initializeWith(modelField);
                } else { field.wasFound = false; }    
            } else {
                field.defaultValue = fromModel(fieldName);
            }
        }) 
        return this;
    } 
    receiveAll(includeKeys) {
        return Array.from(includeKeys ? this.fields.entries() : this.fields.values());
    }
    receive(...fields) {
        return fields.map(field => this.fields.get(field));
    }
    receiveOne(field) {
        return this.fields.get(field);
    }
}

export {StudentFieldsForm, DocumentFields}