import React, {useEffect, useState, Fragment} from "react";
import axios, {CancelToken} from "axios";

function StudentContainer({sources, notFound, loading, timestamp, children}) {
    const [apiCalls] = useStudentApi({sources, timestamp});
    return (
        <>
            {apiCalls.loading
            ? loading
            : <StudentDisplay 
            students = {apiCalls.students} 
            notFound = {notFound}
            formatAs = {children}
            />}
        </>
    )
}

function StudentDisplay({students, notFound, formatAs}) {
    return students 
    ? students.map(student => (
        <Fragment key = {student.uuid}>{formatAs(student)}</Fragment>
    ))
    : <p>{notFound}</p>;
}

function useStudentApi({sources, timestamp}) {
    const [apiCalls, setApiCalls] = useState({loading: true, students: null});
    useEffect(() => {
        let cancel = CancelToken.source();
        Promise.all(sources.map(source => 
            axios.get(`/api/students${source}`, {cancelToken: cancel.token})
        ))
        .then(responses => { 
            const responseStudents = responses.reduce((students, response) => (
                students.concat(Array.isArray(response.data) ? response.data : [])
            ), []);
            const mapped = new Map(responseStudents.map(student => [student.uuid, student]));
            const unique = Array.from(mapped.values());
            const students = unique.length > 0 ? unique : null;
            setApiCalls({loading: false, students});
         })
        .catch(e => /*axios.isCancel(e)*/ undefined); //DO SOMETHING
        return () => cancel.cancel();
    }, [sources, timestamp]);
    return [apiCalls];
}

export default StudentContainer;