import React, {Fragment, useContext} from "react";
import axios from "axios";
import {Toggleable, ToggleContext} from "../toggleable/Toggleable.js";
import {ToggledState} from "../toggleable/ToggledState.js";
import {TogglerButton} from "../toggleable/Toggled.js";
import {StudentContext} from "../../context/StudentContext.js";

function StudentPrograms({toRegister, fields, onStudentInput}) {
    const programs = fields.receiveAll();
    return (
        <div className = "student-programs">
            <h3>Programas</h3>
            {programs.map(program => {
                return (
                <Fragment key = {`program-${program.name}`}> 
                    Programa: <span className = "program-name">{program.label}</span>
                    {toRegister && <StudentProgramSub programFields = {program} onStudentInput = {onStudentInput}/>}
                    {!toRegister && <StudentProgramUpdate wasFound = {program.wasFound} programFields = {program} onStudentInput = {onStudentInput}/>}
                </Fragment>
                );
            })}
        </div>
    );
}

function StudentProgramUpdate({wasFound, programFields, onStudentInput}) {
    if (wasFound === true || wasFound === false)
        return <StudentProgramSub wasFound = {wasFound} programFields = {programFields} onStudentInput = {onStudentInput} />;
    else return <span>...</span>;
}

function StudentProgramSub({wasFound, programFields, onStudentInput}) {
    const initialStatus = !wasFound ? 1 : 3;
    return (
        <Toggleable initialStatus = {initialStatus}>
            <ToggledState status = {[1]}>
                    <TogglerButton type = "button" caption = "Inscribir" status = {2} />
            </ToggledState>
            <ToggledState status = {[2, 3]}>
                <StudentProgramFields programFields = {programFields} onStudentInput = {onStudentInput} />
            </ToggledState>
            <ToggledState status = {[3]}>
                <StudentProgramUnsub programFields = {programFields} />
            </ToggledState>        
        </Toggleable>
    );
}

function StudentProgramUnsub({programFields}) {
    const program = programFields.name;
    const studentUUID = useContext(StudentContext).uuid;
    const toggleable = useContext(ToggleContext);
    function onStudentUnsub() {
        axios.delete(`/api/students/programs?uuid=${studentUUID}&program=${program}`)
        .then(response => {
            programFields.reset();
            toggleable.setStatus(1);
        })
        .catch(err => null)
    }
    return (
        <div className = "button-unsub-student">
            <button type = "button" onClick = {onStudentUnsub}>Dar de Baja</button>
        </div>
    );
}

function StudentProgramFields({programFields, onStudentInput}) {
    const fields = programFields.receiveAll();
    return <>
        {fields.map(field => {
            const defaultValue = field.type === "date" && field.defaultValue
                ? field.defaultValue.split("T")[0]
                : field.defaultValue;
            return <label key = {field.label}>
                {field.label} 
                <input 
                    type = {field.type}
                    name = {field.name}
                    defaultValue = {defaultValue}
                    onInput = {onStudentInput}
                />
            </label>
        })}
    </>
}

export default StudentPrograms;