import React, {useContext, useRef, useState} from "react";
import axios from "axios";
import {StudentContext} from "../../context/StudentContext.js";

function StudentFileUpload({initialSource, filetype, renderFile}) {
    const studentUUID = useContext(StudentContext).uuid;
    const [source, setSource] = useState({src: initialSource, timestamp: Date.now()});
    const fileToUpload = useRef();
    function onFileSelect(event) { fileToUpload.current = event.target.files[0]; }
    function onFileUpload(event) { 
        event.preventDefault();
        let config = {headers: {"Accept": "application/json", "Content-Type": `${fileToUpload.current.type}`}};
        axios.post(`/api/students/upload/${filetype}?uuid=${studentUUID}`, fileToUpload.current, config)
        .then(response => {
            const source = response.data;
            setSource({src: source.src, timestamp: Date.now()});
        });    
    }
    return (
        <>
            {renderFile && renderFile(source.src, source.timestamp)}
            <form onSubmit = {onFileUpload}>
                <input type = "file" onInput = {onFileSelect}/>
                <button type = "submit"> Subir </button>
            </form> 
        </>
    );
}

function StudentPhoto({initialSource}) {
    const fileType = "photo";
    function onPhotoError(event) { event.target.src = `./media/students/${fileType}/placeholder.jpg`; }
    return (
        <StudentFileUpload initialSource = {initialSource} filetype = {fileType}
            renderFile = {(source, timestamp) => {
                return <div className = "student-photo">
                    <img className = "student-avatar"
                        src = {source
                        ? `/media/students/${source}?${timestamp}` 
                        : `/media/students/photo/placeholder.jpg?${timestamp}`} 
                        alt = "student-avatar"
                        onError = {onPhotoError}
                       />
                </div>;
            }}   
        />     
    );
}

function StudentReport({initialSource}) {
    const fileType = "report";
    return (
        <StudentFileUpload initialSource = {initialSource} filetype = {fileType}
            renderFile = {(source, timestamp) => {
                return (
                    <div className = {`student-download-report ${source ? "active" : "inactive"}`}>
                        {source
                        ? <a className = "student-download-content" 
                        href = {`/media/students/${source}`}
                        download = {`boleta`}>
                            {`Descargar boleta: ${new Date().toISOString()}`}
                        </a>
                        : <p className = "student-download-content">
                            {"No has subido ninguna boleta"}
                        </p>}
                    </div>
                );
            }}   
        />     
    );
}

export {StudentPhoto, StudentReport};