import React, {useState} from "react";
import axios from "axios";
import useInput from "../../hooks/useInput.js";

function LoginForm({renderError}) {
    const [loginError, setLoginError] = useState({error: null, issued: 0});
    const [loginRef, onLoginInput] = useInput();
    function onLogin(event) {
        event.preventDefault();
        const login = loginRef.current;
        axios.post("/login/user", login)
        .then(response => window.location.href = "/")
        .catch(err => setLoginError(previous => (
            {error: {failure: err, issuedAt: Date.now(), issuedToken: previous.issued + 1},
            issued: previous.issued + 1}
        )));
    }
    return (
        <form onSubmit = {onLogin}>
            <label>
                Usuario: 
                <input type = "text" name = "username" required onInput = {onLoginInput} />
            </label>
            <label>
                Contraseña: 
                <input type = "password" name = "password" required onInput = {onLoginInput} />
            </label>
            <button type = "submit">Ingresar</button>
            {(renderError && loginError.error) && renderError(loginError.error)}
        </form>
    );
}


export default LoginForm;