import React from "react";
import useInput from "../hooks/useInput.js";
import {StudentFieldsForm} from "../components/apps/StudentFields.js";

function withSubmit(useSubmit) {
    return function StudentForm({fields, renders = {}}) {
        const [studentRef, onStudentInput] = useInput();
        const [studentForm, onStudentSubmit] = useSubmit(studentRef, fields);
        return (
            <div className = "app-register">
                <form onSubmit = {onStudentSubmit}>
                    <StudentFieldsForm toRegister = {studentForm.toRegister} fields = {studentForm.fields} onStudentInput = {onStudentInput}/>
                    <button type = "submit" className = "register-btn">Subir</button>
                    {renders.submitted && renders.submitted(studentForm.submitted)}
                    {(renders.error && studentForm.error) && renders.error(studentForm.error)}
                </form>
            </div>
        );
    }
}


export default withSubmit;