import {useEffect, useState, useContext} from "react";
import {StudentContext} from "../context/StudentContext.js";
import axios from "axios";

function useRegularFields(studentFields) { return [studentFields]; }

function useInitializedFields(studentFields, counts) {
    const [initializedFields, setInitializedFields] = useState(studentFields);
    const studentUUID = useContext(StudentContext).uuid;
    useEffect(() => {  
        let canceller;
        function fetchFieldData() {
            axios.get(`/api/students?uuid=${studentUUID}`, {cancelToken: new axios.CancelToken(cancelToken => canceller = cancelToken)})
            .then(response => { 
                if (!Array.isArray(response.data) || response.data.length !== 1) throw new Error();
                const student = response.data[0];
                const initialized = studentFields.initializeWith(student);
                setInitializedFields(Object.assign(Object.create(initialized.__proto__), initialized));
            })
            .catch(e => { return null; });
        }
        fetchFieldData(); 
        return () => canceller();
    }, [studentUUID, counts.submittedCount, counts.errorCount, studentFields]);
    return [initializedFields];
}

export {useRegularFields, useInitializedFields};